<template>
  <div class="search">
    <keep-alive>
      <HomeLink></HomeLink>
    </keep-alive>
    <div class="top">
      <i class="fas fa-search"></i>
      <input
        type="text"
        v-model="keyword"
        placeholder="搜索歌曲、歌手、专辑"
        @keyup.enter="searchShow = true"
      />
      <i class="dele" @click.stop=" keyword = null;searchShow = false"></i>
    </div>
    <!-- 实时搜索 -->
    <template v-if="songs && songs != '' ">
      <div class="recom">
        <!-- @click="keyword = null" -->
        <ul>
          <template v-if="searchShow">
            <SearchList
              v-for="(item, index) in songs"
              :item="item"
              :key="index"
              :options="{info: true}"
              :currentSong="currentSong"
              @getSong="$emit('getSong', $event)"
            ></SearchList>
          </template>
          <template v-else>
            <h3>搜索"{{keyword}}"</h3>
            <li v-for="(item,index) in songs" :key="index" class="searchLi">
              <i class="fas fa-search"></i>
              <span>{{item.name}}</span>
            </li>
          </template>
        </ul>
      </div>
    </template>

    <template v-else>
      <div class="default">
        <div class="hotSearch">
          <h3>热门搜索</h3>
          <ul>
            <li v-for="(item, index) in hots" :key="index">{{item.first}}</li>
          </ul>
        </div>
        <div class="historySearch">
          <ul>
            <li>
              <i class="fas fa-history"></i>
              <span>789</span>
              <i class="dele"></i>
            </li>
            <li>
              <i class="fas fa-history"></i>
              <span>789</span>
              <i class="dele"></i>
            </li>
            <li>
              <i class="fas fa-history"></i>
              <span>789</span>
              <i class="dele"></i>
            </li>
          </ul>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
import HomeLink from "@/components/HomeLink.vue";
import SearchList from "@/components/SearchList.vue";

export default {
  props: {
    currentSong: Object
  },
  data() {
    return {
      keyword: null,
      songs: null,
      hots: null,
      searchShow: false
    };
  },
  components: {
    HomeLink,
    SearchList
  },
  methods: {
    // 实时搜索
    translatekeyword() {
      // console.log(keyword)
      // 根据关键词拿数据
      if (this.keyword) {
        this.axios
          .get("search", {
            params: {
              keywords: this.keyword,
              limit: 6
            }
          })
          .then(response => {
            console.log(response.data.result.songs);
            this.songs = response.data.result.songs;
          })
          .catch(error => {
            console.log(error);
          });
      }
    },

    //热搜列表(简)
    hotsearch() {
      this.axios
        .get("search/hot")
        .then(response => {
          console.log(response.data.result.hots);
          this.hots = response.data.result.hots;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  mounted() {
    this.hotsearch();
  },
  watch: {
    keyword() {
      this.songs = [];
      this.translatekeyword();
      this.searchShow = false
    }
  }
};
</script>
